<template>
	 <view class='body'>
		<view class="title">村事宝用户协议</view>
		<view class="content">
			<view class="p">重要须知：村事宝微信小程序端（以下统称“村事宝平台”或“本平台”）所有权人及其关联方（下称“村事宝”）在此特别提醒您仔细阅读（未成年人应在其法定监护人的陪同下阅读）本《用户协议》（下称“本协议”）中的各个条款，尤其是以粗体并下划线标示的条款，<text class="weight">包括但不限于免除或者限制村事宝责任的条款、对用户权利进行限制的条款以及约定争议解决方式、司法管辖的条款。您有权选择同意或者不同意本协议。</text></view>
			<view class="p"><text class="weight">您与村事宝均应当严格履行本协议及其补充协议所约定的各项义务，如发生争议或者纠纷，双方可以友好协商解决;协商不成的，任何一方均可向本协议签订地有管辖权的人民法院提起诉讼。本协议签订地为杭州市下城区。</text></view>
			<view class="p">您如果通过登录村事宝平台用户注册页面或者村事宝提供的其他用户注册渠道注册用户账号，完成我们的注册流程并通过点击同意的形式在线签署以下协议即视为您完全同意本协议，愿意接受本协议所有及任何条款的约束。<text class="weight">如您不同意本用户协议，您应不得使用或主动取消村事宝所提供的服务。</text></view>
			<view class="h3">第一条 协议内容及生效</view>
			<view class="p">1.1 本用户协议内容包括以下所有正文及村事宝已经发布或将来可能发布的各类规则、解释、声明、通告等。所有上述规则、解释、声明及通告等都被视为本用户协议的组成部分，具有同等的法律效力。</view>
			<view class="p">1.2 用户登录、使用及连接村事宝服务均被视为用户已完全了解、接受并同意遵守本用户协议的所有内容。村事宝有权在必要时单方修改用户协议，并通过网站和/或者客户端发布修改变更，不再另行单独通知。修改后的协议一经发布即代替旧版本协议，自动更新为用户与村事宝之间的适用协议。如在用户协议更新并生效后，用户继续使用村事宝服务，则视为您已完全了解、接受用户协议的更新并同意遵守更新后的用户协议。如用户不接受村事宝更新后的用户协议，请立即停止使用村事宝服务。</view>
			<view class="p">1.3 如果您未满18周岁，必须在父母或者监护人监护下使用村事宝平台。</view>
			<view class="h3">第二条 用户账号</view>
			<view class="p">2.1 您确认，在您开始注册程序使用村事宝平台服务前，您应当具备中华人民共和国法律规定的与您行为相适应的民事行为能力。<text class="weight">若您不具备前述与您行为相适应的民事行为能力，则您及您的监护人应依照法律规定承担因此而造成的一切后果。</text></view>
			<view class="p">2.2 您可以通过登录本平台的用户账号注册页面注册用户账号并设置相应的登录密码，一经注册成功，您即可以凭借用户账号及登录密码登录及使用本平台。就目前而言，上述用户账号是您的移动电话号码。</view>
			<view class="p">2.3 村事宝平台只允许每位用户使用一个村事宝平台账户。如有证据证明或村事宝根据村事宝平台规则判断您存在不当注册或不当使用多个村事宝平台账户的情形，村事宝平台可以采取冻结或关闭账户、取消订单、拒绝提供服务等措施，如给村事宝平台及相关方造成损失的，您还应承担赔偿责任。</view>
			<view class="p">2.4 您务必妥善保管您的用户账号、登录密码、支付密码(如有)及您在其项下填写的所有个人信息。<text class="weight">账户因您主动泄露或因您遭受他人攻击、诈骗等行为导致的损失及后果，本平台并不承担责任，您应通过司法、行政等救济途径向侵权行为人追偿。除本平台存在过错外，您应对您账户项下的所有行为结果(包括但不限于在线签署各类协议、发布信息、购买商品及服务及披露信息等)负责。</text></view>
			<view class="p">2.5 为了防止您及其他用户的用户账号及其项下的个人信息泄露或者被他人窃取，村事宝可能会随时采取计算机病毒查杀技术、计算机加密技术等措施进行保护。对此，您完全同意并接受，但这并不能免除本协议第3.5条所述的、您对用户账号及其项下的个人信息所负有的妥善保管义务。</view>
			<view class="p">2.6 由于村事宝平台账户关联您的个人信息及平台商业信息，您的村事宝平台账户仅限您本人使用。仅当有法律明文规定、司法裁定或经本平台同意，您方能进行账户的转让。您的账户一经转让，该账户项下的权利义务一并转移。除此外，您的账户不得以任何方式转让，否则本平台有权追究您的违约责任，由此产生的一切责任由您承担。</view>
			<view class="p">2.7 您如果遗忘了密码或者密码被他人修改，您可以按照村事宝公布的账号及密码找回规则，通过村事宝提供的账号及密码找回系统程序进行找回，或者在第一时间联系村事宝的客服人员协助找回，但村事宝不保证一定可以找回。</view>
			<view class="p"><text class="weight">2.8 您充分理解并完全同意:根据您的用户账号下的产品、服务购买记录及其他相关信息，平台如果发现或者合理怀疑您此前或者当次通过本平台或者村事宝提供的其渠道购买的产品、服务有出现并非用于个人使用用途的情形，对于您的这个用户账号的产品、服务购买需求，平台有权不予接受;您如果已经下达订单，平台有权不予接受订单;平台如果已经接受订单的，有权单方面解除。而且，村事宝视情况还有权冻结您的用户账号，使之无法通过本平台以及村事宝提供的其他渠道下达订单、购买产品和服务。</text></view>
			<view class="h3">第三条 用户信息</view>
			<view class="p">3.1 用户个人信息包括但不限于下列信息：用户真实姓名、头像、身份证号、职业、职务、手机电话、银行账号、第三方支付账号、电子邮件、教育经历、从业经历等。</view>
			<view class="p">3.2 用户个人健康和医疗信息。用户根据健康的特殊要求而提交的个人身高、体重、性别、年龄、血型以及慢性病史情况等个人健康和医疗信息。如果您是为您不满 14 周岁的子女使用村事宝的产品和服务的，请您务必确认您是合法监护人后方可向村事宝提供您子女的个人信息，您为该等信息提供行为承担全部责任。</view>
			<view class="p">3.3 非用户个人信息。用户在村事宝上，包括提问、回答、浏览、操作状态、使用记录、使用习惯等在内的全部记录信息。除3.1、3.2款所列用户个人信息及个人健康和医疗信息范围外的其他所有信息，均视为非用户个人信息。</view>
			<view class="p">3.4 第三方平台记录信息。目前使用腾讯微信登录使用的，以及未来可能使用新浪微博等第三方平台账号注册、登录、使用村事宝服务的，将被视为用户完全理解、同意并接受村事宝包括但不限于收集、统计、分析等方式使用其在新浪微博、腾讯微信等第三方平台填写、登记、公布、记录的全部信息。用户一旦使用第三方平台账号注册、登陆、使用村事宝服务，村事宝对该第三方记录信息的任何使用，均被视为已经获得了用户本人的完全同意并接受。</view>
			<view class="p">3.5 用户在村事宝所涉及的包括个人真实姓名、手机电话、电子邮件、通信地址等在内的用户个人信息、第三方平台账号信息、非用户个人信息以及为咨询而提供的个人健康和医疗信息，村事宝在此承诺，均予以严格保密，未经用户明确授权和允许，不向任何第三方/第三人披露上述用户隐私信息，法律另有规定的除外。</view>
			<view class="p">3.6 用户应本着诚实守信的原则，自行向村事宝提供所需信息。回答者即执业，需按村事宝的要求进行注册并通过认证并通过有关互联网执业备案，方可在村事宝平台提供问答服务。具体注册认证为所需提供的信息和资料等由用户按村事宝的的要求进行提供，相关要求以符合法律法规要求的在线问诊注册的资质为最最低要求；提问者需提供个人信息、就医经历以及相关的其他必要信息；用户需保证所提供的信息必须合法、真实、准确、完整，并保证及时更新以上信息。村事宝将认为任何由用户提供的信息，都属于用户自己或用户已获得了合法授权可以向村事宝提供。如因用户自行提供的信息存在非法、抄袭、错误、侵犯他人隐私等问题，用户需承担因此引发的相应的法律责任及后果，且村事宝保留终止用户使用村事宝各项服务的权利，同时村事宝可以保留向被侵权的第三方披露此类用户的个人信息之相关权利。</view>
			<view class="p">3.7 用户应自行维护个人的村事宝平台之上的账户和密码安全，并对此账户在村事宝上的所有行为负完全责任。用户不得将账户转借、租让、售卖给他人使用，否则应承担由此可能产生的全部责任，并与实际使用人承担连带责任。用户应对任何人利用您的账户和密码所进行的活动负完全责任，村事宝无法对非法或未经您授权使用您的账户及密码的行为做出甄别，因此村事宝不承担任何责任。当密码或者账户遭到未获授权使用，或者发生任何安全问题时，用户有责任及时有效地通知村事宝并向当地公安机关报案。</view>
			<view class="p">3.8 用户信息使用。用户在使用过程中发现任何不妥或者不满意之处，有权根据《村事宝隐私政策》中约定的方式向村事宝提出申请，进行相关信息删除等处理，村事宝不承担主动删除、销毁用户信息的责任。</view>
			<view class="p">3.9 为向用户提供服务，村事宝将在合理范围内使用用户个人信息、用户个人医疗信息、非用户个人信息以及第三方平台记录信息。用户一旦注册、登录、使用村事宝服务，将被视为村事宝已获得包括但不限于收集、统计、分析、商业用途等方式使用用户信息的权利。村事宝对用户信息的使用无需其他意思表示，无需向用户支付任何费用。</view>
			<view class="p">3.10 <text class="line">您一经注册用户账号，即视为您同意村事宝及/或其关联公司通过短信或者电子邮件的方式向您注册时填写的手机号码发送相应的订单信息及广告信息、促销优惠等营销信息;您如果不同意发送，您可以通过相应的退订功能进行退订。</text></view>
			<view class="p">3.11 您使用或继续使用我们的服务，即意味着同意我们按照《村事宝隐私政策》收集、使用、储存和分享您的相关信息。相关个人隐私政策的收集及使用规则，请参见<text class="agree" @click="getAgreement">《村事宝隐私政策》。</text></view>
			<view class="p">3.12村事宝非常重视对用户个人隐私的保护。用户所提供的个人信息等资料以及村事宝所保留的用户个人资料，将受到中国有关隐私的法律以及《村事宝隐私政策》的特别保护，详细的隐私保护及相关条款详见《村事宝隐私政策》</view>
			<view class="p">3.13村事宝有权对用户提供的信息按照《村事宝隐私政策》的规定进行查看、处理、传播和使用。随着服务的变化，村事宝有权对隐私条款不时进行修改，且不再另行通知。更新后的隐私声明一旦发布即有效代替旧版本的隐私声明，您在访问和使用村事宝时，即表示您已同意遵守并接受最新的隐私政策。村事宝在此特别建议您经常关注隐私条款的变更。</view>
			<view class="h3">第四条 服务</view>
			<view class="p">本协议所称「服务」，指用户可通过村事宝所获取的全部服务，包括但不限于在线咨询问答服务，免费医药信息搜索获取服务以及药品服务等村事宝平台现有的以及将来新开发的所有产品和服务内容。对于村事宝平台所提供的所有服务，和提问者或服务接受者之间不得私下交易。如有违背，村事宝有权拒绝支付交易金额、处以罚款以及暂停或者永久性关闭相关账户的权利，且村事宝不承担任何责任。</view>
			<view class="p">4.1村事宝平台上可提供在线问答服务。</view>
			<view class="p">4.2村事宝平台上问答服务的咨询范围和定价均由决定。收到提问后，有权选择退款不回答，但需给出拒绝理由。若24小时内未回答，平台将退款给提问者。一旦回答后，则视为协议成立，提问者向支付的提问费用，不可返还或撤销。</view>
			<view class="p">4.2.1 用户在问答服务中下单时须仔细确认您咨询的、购买的问题。一旦完成付款，即被视为用户已同意购买对应的服务。</view>
			<view class="p">4.3 应确保在自身专业领域具备相关专业知识或经验，并向村事宝提交相关证明。只限于根据提问者的主观描述，尽其所能利用自身专业领域所积累的知识和经验，对提问者给以解答或建议。需对自己提供给提问者的回答以及服务质量负责，不得提供超出个人专业领域以及能力的服务。如有违背，由此引发的纠纷完全由自行承担。村事宝在此声明，不对回答结果是否符合用户预期做出任何保证，不承担任何责任。</view>
			<view class="p">4.4村事宝为用户提供药品服务，应以专业而谨慎的态度为用户提供用药建议，并对自己出具的用药建议负责。该等用药建议如被用户采纳，用户可自愿选择村事宝提供的药品服务获取药品。村事宝仅为药品符合相关质量要求负责，不对用药的合理性或专业性负责 对于村事宝提供的其它的关于健康咨询或健康测评指导等个性化定制服务，用户和均应遵守村事宝与用户之间签署的关于该等个性化服务的相关用户协议（如有）。</view>
			<view class="p">4.5 所提供的建议及回答均系个人建议及经验，提问者需知晓并同意此类建议及个人经验仅供参考。若将的回答作为诊断，由此可能引发的任何负面影响甚至损失由提问者自行负责，村事宝平台不予以负责。同时提问者在此声明同意不会向村事宝及提出任何性质的索赔。用户应谨慎选择药品服务，如用户自愿选择接受的建议选择村事宝上的药品服务的，除非药品质量问题，否则由用户和承担因药品服务而产生的一切后果。</view>
			<view class="p">4.6提问者和在交流过程中应该要文明用语、互相尊重、平等交流。提问者不得对进行辱骂、恐吓等行为；不得将提问者信息公开给其他人，或有其他故意伤害提问者行为。如发生以上行为，村事宝有权终止相关方的所有权利，如有相关的争议发生，村事宝将在尊重事实及法律的基础上以合理形式支持被侵权方进行维权。</view>
			<view class="p">4.7<text class="line">为保障服务能在村事宝的监督下有效进行，防止因私自提供服务导致的不利后果不可控制，禁止和提问者之间进行私下沟通联系。如村事宝发现要求私下与提问者沟通接触的，有权给予该警告，警告后仍不改正的，村事宝有权封禁该的账号，禁止其在村事宝提供服务。 如村事宝发现提问者私自要求绕开村事宝提供服务的，村事宝将提醒或警告该提问者，经提醒后仍不改正的，村事宝有权封禁该提问者的账号，禁止其通过村事宝获取服务。</text></view>
			<view class="h3">第五条 费用、支付及税款缴纳</view>
			<view class="p">5.1 用户如需向村事宝平台上提问，必须委托村事宝向支付咨询费用。</view>
			<view class="p">5.2 用户须遵守村事宝的定价条款规则，拥有设定、调整价格的权利，并授权委托村事宝代为向提问者收取咨询费用。</view>
			<view class="p">5.3村事宝有权调整付费咨询所涉及的计费方式和收费方式，以及结算时间频率。</view>
			<view class="p">5.4村事宝平台有权对在村事宝所获得的问诊收入进行一定比例的抽成，以支持平台服务的有序运行。村事宝平台会根据实际运营策略，不定期调整抽成比例，继续使用村事宝提供问答服务，即视为接受村事宝的价格策略，如比例发生变更，村事宝不再另行通知。</view>
			<view class="p">5.5 根据我国法律及税收政策，平台所有用户有义务就本人在平台上的劳务报酬所得（包括但不限于提问收入）按时、足额的缴纳个人所得税。村事宝暂时采取用户个人自行申报并缴纳税款的形式执行。村事宝将随时根据税务部门的最新政策或指导意见，即时调整用户个人所得税税款缴纳问题的执行方式。</view>
			<view class="p">5.6 若相关税务部门要求村事宝提供用户账户信息以核实用户自行申报的在平台收入具体数额，平台有权不经用户同意而进行披露，并根据国家法律配合相关税务部门的其他合法要求。</view>
			<view class="p">5.7 用户使用第三方支付方式所产生的手续费等由用户自行承担。</view>
			<view class="p">5.8 对于村事宝的评价体系，用户不可采取任何损坏、破坏或操纵的行为。</view>
			<view class="p">5.9 用户不得利用平台规则漏洞牟取任何不正当收益的行为，包括但不限于利用问诊优惠服务反复或重复刷单。</view>
			<view class="h3">第六条 村事宝与用户双方的权利与义务</view>
			<view class="p">6.1 基于交易纠纷、技术原因等因素，村事宝保有查看、复制、审查交易过程及内容的权利。</view>
			<view class="p">6.2村事宝有权根据不同情况选择保留或删除相关信息或继续、停止对该用户提供服务，并追究相关法律责任。</view>
			<view class="p">6.3为便于和患者之间的沟通，村事宝有义务维护平台服务的正常运行，提升及改进技术。</view>
			<view class="p">6.4 提问者对咨询内容不满意，有权利向村事宝提出投诉或进行申诉，村事宝有义务依据实际情况开展协调沟通。如因在村事宝上发生服务纠纷，村事宝有义务向双方了解相关信息，进行沟通、调解；司法部门或行政部门依照法定程序要求村事宝提供相关数据，村事宝将积极配合并提供有关资料。</view>
			<view class="p">6.5为保证服务质量，村事宝提供客服邮箱、电话、在线交谈等服务。为保证服务质量，保障交易过程及内容的可追溯性，村事宝保有客服与用户服务过程中全程录音的权利，村事宝承诺不主动将录音内容泄露给第三方，但基于交易纠纷、技术原因等因素，村事宝保有复制、审查服务过程中录音内容的权利。</view>
			<view class="h3">第七条 用户守则</view>
			<view class="p">7.1本平台与其他在线使用的互联网网站一样，您如果需要进行下载、安装、运行、登录或者使用，您至少必须自备一部可供上网的智能移动终端，并确保其能够通过互联网与本平台服务器软件进行实时的信息(即电子数据)交互，相应的上网流量费等相关费用由您自行承担。除非另有说明，村事宝存储在其服务器上的数据是村事宝和其用户之间交易的唯一有效证据。</view>
			<view class="p">7.2 本平台目前免费使用，村事宝有权对本平台进行升级及更新，自行设置收费的软件功能、费率标准、收费对象及/或收费时段。您如果不同意村事宝设置的软件功能、费率标准、收费对象及/或收费时段的，则应当立即停止使用本平台;您如果继续使用的，则视为您接受村事宝的上述设置，您应当遵照履行并向村事宝支付相应的费用。</view>
			<view class="p">7.3您不得进行任何侵犯本平台知识产权的行为，或者进行其他的有损于村事宝或其他用户合法权益的行为，包括但不限于:</view>
			<view class="p">（1）删除或修改本平台上的版权信息；</view>
			<view class="p">（2）修改、复制和/或发行本平台；</view>
			<view class="p">（3）在本平台当中内置各种插件程序或者其他的第三方程序；</view>
			<view class="p">（4）进行编译、反编译、反向工程或者以其他方式破解本平台的行为；</view>
			<view class="p">（5）使用本平台外挂和/或利用本平台当中的 BUG 来获得不正当的利益；</view>
			<view class="p">（6）利用劫持域名服务器等技术非法侵入、破坏本平台的服务器软件系统，或者修改、增加、删除、窃取、截留、替换本平台的客户端和/或服务器软件系统中的数据，或者非法挤占本平台的服务器空间，或者实施其他的使之超负荷运行的行为；</view>
			<view class="p">（7）利用本平台故意传播恶意程序或计算机病毒。</view>
			<view class="p">7.4您充分理解到:村事宝可能会不定期地通过发布软件升级包或软件补丁、在线升级等方式对本平台进行更新。更新的过程中，村事宝可能通过互联网调取、收集您的关于本平台的客户端软件版本信息、数据及其他有关资料，并自动进行替换、修改、删除和/或补充。此种行为是平台更新所必须的一种操作或步骤，您如果不同意村事宝进行此种操作，请您不要进行更新;您更新的行为即视为您同意村事宝进行此种操作。</view>
			<view class="p">7.5您有权在本平台当中发表评论，但您应确保评论真实、客观且不会侵犯任何第三方的著作权、肖像权、名誉权、隐私权等合法权利。而且，您不得借助本平台用户评论功能发布任何广告。</view>
			<view class="p">7.6您应当遵守国家有关法律法规，不得在本平台当中发表或者传播含有下列内容的信息:</view>
			<view class="p">（1）反对宪法所确定的基本原则的；</view>
			<view class="p">（2）危害国家安全、泄露国家秘密、颠覆国家政权、破坏国家统一的；</view>
			<view class="p">（3）损害国家荣誉和利益的；</view>
			<view class="p">（4）煽动民族仇恨、民族歧视，破坏民族团结的；</view>
			<view class="p">（5）破坏国家宗教政策，宣扬邪教和封建迷信的；</view>
			<view class="p">（6）散布谣言，扰乱社会秩序，破坏社会稳定的；</view>
			<view class="p">（7）散布淫秽、色情、赌博、暴力、凶杀、恐怖或者教唆犯罪的；</view>
			<view class="p">（8）侮辱或者诽谤他人，侵害他人合法权益的；</view>
			<view class="p">（9）含有法律、行政法规禁止的其他内容的。</view>
			<view class="p">7.7<text class="line">您充分理解并完全同意:对您在本平台当中发表或者传播的评论及其内容，包括但不限于视频、照片、文字内容，不论是否属于著作权法意义上的作品，村事宝均享有永久的、无期限及地域限制的、完全免费的使用权。而且，对于上述评论和/或内容，村事宝还享有下列各项权利，且实际行使时无须另行征得您的同意:</text></view>
			<view class="p"><text class="line">（1）有权对其进行修改、编辑、汇编、改编、分发或者以其他的方式进行使用；</text></view>
			<view class="p"><text class="line">（2）既有权使用其全部内容，也有权使用其中的某一部分或某几部分内容；</text></view>
			<view class="p"><text class="line">（3）有权将其许可给任何第三方使用，被许可的第三方征得村事宝同意后，既可以将其使用于村事宝 App 等村事宝自有网络平台，也可以将其使用于被许可的第三方自有的网络平台。</text></view>
			<view class="p"><text class="line">（4）有权单独以自己的名义，对未经村事宝许可而擅自使用上述评论和/或内容的第三方进行取证、证据保全、公证、提起诉讼和/或依法采取其他合法措施，以追究其法律责任。</text></view>
			<view class="p">7.8个人账户是村事宝授权您使用村事宝平台用户管理系统的唯一凭证。您同意在符合法律法规及本条款规定的情况下使用个人帐户，村事宝有可能在某些情况(包括但不限于您违反有关法律法规规定或本协议和/或其它公开规则，或者您严重违背社会公德、提供虚假注册身份信息、经村事宝判定认为存在恶意退货或其他损害或经本平台判定认为可能损害村事宝利益的不正当行为等)下暂时冻结、永久冻结、修改、删除您的个人账户或者采取其他处理措施。非经村事宝同意，您不得将村事宝各项产品及服务用于商业用途(例如:销售村事宝个人帐户、销售个人帐户下积分等）。在使用村事宝服务过程中，如果您或您的个人帐户刻意规避村事宝管理措施，或存在涉嫌欺诈、商业牟利、不恰当或不诚实地使用服务或者其他违反本协议和/或其他公开规则的行为，村事宝有权拒绝为您继续提供服务，永久冻结您的个人帐户。</view>
			<view class="p">7.9如您的行为使村事宝及/或其关联公司遭受损失(包括自身的直接经济损失、商誉损失及对外支付的赔偿金、和解款、律师费、诉讼费等间接经济损失)，您应赔偿村事宝及/或其关联公司的上述全部损失。</view>
			<view class="p">如您的行为使村事宝及/或其关联公司遭受第三人主张权利，村事宝及/或其关联公司可在对第三人承担金钱给付等义务后就全部损失向您追偿。</view>
			<view class="p">如因您的行为使得第三人遭受损失，村事宝及/或其关联公司出于社会公共利益保护或消费者权益保护目的，可自您的任何村事宝账户中划扣相应款项进行支付。</view>
			<view class="h3">第八条 免责声明</view><view class="p">（1）用户应遵守国家的有关法律、法规和行政规章制度。如有用户违反国家法律法规或本用户协议，村事宝有权停止向用户提供服务不需要承担任何责任；如用户的不法使用行为导致村事宝或村事宝的合作方，其它用户遭受任何损害或者遭受任何来自第三方的纠纷、诉讼、索赔要求等，用户须向村事宝赔偿相应的损失，用户需对其违反用户协议所产生的一切后果承担全部法律责任；</view>
			<view class="p">（2）由于您将用户密码告知他人或与他人共享注册账户，由此导致的任何个人信息的泄露，或其他非因村事宝方面原因导致的个人信息的泄露；</view>
			<view class="p">（3）根据法律规定或政府相关政策要求提供您的个人信息；</view>
			<view class="p">（4）任何通过黑客攻击、电脑病毒侵入等非法截取、访问等方式从我们网站上获取的信息；</view>
			<view class="p">（5）因台风、地震、海啸、洪水、停电、战争、恐怖袭击等国际通用不可抗力因素导致的任何后果。</view>
			<view class="p">8.5 本协议下的服务行为涉及到互联网及移动通讯等服务，可能受各个环节不稳定因素的影响，比如不可抗力、计算机病毒或者黑客攻击等造成的服务中断或不能满足用户要求的风险，用户须理解和认可，并承担以上风险。基于上述已经提及的可能发生的各环节不稳定因素影响，村事宝在此声明对服务的及时性、安全性、准确性不作担保，对因此导致用户不能接收信息，或者传递错误等问题不承担任何责任，希望用户理解并认可。</view>
			<view class="p">8.6 如因村事宝平台的自有系统发生故障而影响到本服务的正常运行，村事宝承诺第一时间内与相关单位配合，及时处理并进行修复。但用户因此而产生的经济损失，村事宝不承担责任。此外，村事宝保留不经事先通知为维修保养、升级或其他目的暂停本服务任何部分的权利。</view>
			<view class="p">8.7 在使用本协议下服务的过程中，可能会遇到网络信息或其他用户的行为所带来的风险，村事宝不对任何信息的真实性、适用性、合法性承担责任，也不对因侵权行为给您造成的损害负责。这些风险包括但不限于：</view>
			<view class="p">（1）来自他人匿名或冒名的含有威胁、诽谤、令人反感或非法内容的信息；</view>
			<view class="p">（2）因使用本协议项下的服务，遭受他人误导、欺骗或其他导致或可能导致的任何心理、生理上的伤害以及经济上的损失；以及</view>
			<view class="p">（3）其他因网络信息或用户行为引起的风险。</view>
			<view class="p">第九条 通知与送达</view>
			<view class="p">9.1您在注册成为村事宝平台用户，并接受村事宝平台服务时，您应该向村事宝提供真实有效的联系方式(包括您的电子邮件地址、联系电话、联系地址等)，对于联系方式发生变更的，您有义务及时更新有关信息，并保持可被联系的状态。</view>
			<view class="p">您在注册村事宝平台用户时生成的用于登陆村事宝平台接收站内信、系统消息的会员账号，也作为您的有效联系方式。</view>
			<view class="p">村事宝将向您的上述联系方式的其中之一或其中若干向您送达各类通知，而此类通知的内容可能对您的权利义务产生重大的有利或不利影响，请您务必及时关注。</view>
			<view class="p">9.2送达</view>
			<view class="p">村事宝通过上述联系方式向您发出通知，其中以电子的方式发出的书面通知，包括但不限于在村事宝平台公告，向您提供的联系电话发送手机短信，向您提供的电子邮件地址发送电子邮件，向您的账号发送系统消息以及站内信信息，在发送成功后即视为送达;以纸质载体发出的书面通知，按照提供联系地址交邮后的第五个自然日即视为送达。</view>
			<view class="p">对于在村事宝平台上因交易活动引起的任何纠纷，您同意司法机关(包括但不限于人民法院)可以通过手机短信、电子邮件等现代通讯方式或邮寄方式向您送达法律文书(包括但不限于诉讼文书)。您指定接收法律文书的手机号码、电子邮箱等联系方式为您在村事宝平台注册、更新时提供的手机号码、电子邮箱联系方式，司法机关向上述联系方式发出法律文书即视为送达。您指定的邮寄地址为您的法定联系地址或您提供的有效联系地址。</view>
			<view class="p">您同意司法机关可采取以上一种或多种送达方式向您达法律文书，司法机关采取多种方式向您送达法律文书，送达时间以上述送达方式中最先送达的为准。</view>
			<view class="p">您同意上述送达方式适用于各个司法程序阶段。如进入诉讼程序的，包括但不限于一审、二审、再审、执行以及督促程序等。</view>
			<view class="p">你应当保证所提供的联系方式是准确、有效的，并进行实时更新。如果因提供的联系方式不确切，或不及时告知变更后的联系方式，使法律文书无法送达或未及时送达，由您自行承担由此可能产生的法律后果。</view>
			<view class="h3">第十条 服务变更、中断或终止：</view>
			<view class="p">10.1 因系统维护或升级的需要而需暂停网络服务，村事宝将尽可能事先进行通告。</view>
			<view class="p">10.2 如发生下列任何一种情形，村事宝有权解除本协议，并终止您的全部服务：</view>
			<view class="p">(1) 您违反国家有关法律法规或村事宝管理规定，侵害他人合法权益的；</view>
			<view class="p">(2) 您因在村事宝上的不当行为被行政或司法机构拘留或起诉；</view>
			<view class="p">(3) 您丧失使用网站服务的行为能力；</view>
			<view class="p">(4) 您提供的个人资料不真实；</view>
			<view class="p">(5) 您盗用他人账户、发布违禁信息、骗取他人财物的；</view>
			<view class="p">(6) 您传播虚假信息，歪曲事实，经查证属实的；</view>
			<view class="p">(7) 其它村事宝认为需要终止服务的情况。</view>
			<view class="p">除前款所述情形外，村事宝保留在不事先通知您的情况下随时中断或终止部分或全部网络服务的权利，对于服务的中断或终止而造成您的损失的，村事宝不承担任何责任。</view>
			<view class="p">10.3 服务发生变更、中断、终止后，村事宝仍有以下权利：</view>
			<view class="p">(1) 服务发生终止后，村事宝将您的注册数据及以前的行为记录进行匿名化处理；</view>
			<view class="p">(2) 如您在服务变更、中断、终止前，在村事宝平台上存在违法行为或违反条款的行为，村事宝仍可行使本服务条款所规定的权利。</view>
			<view class="h3">第十一条 其他约定</view>
			<view class="p">11.1村事宝保留随时地不需要任何理由地、单方面地修订本协议的权利。本协议一经修订即完全替代修订前的协议版本，并通过适当的方式向所有用户公开征求意见，您可以通过本平台或本平台指定的方式反馈您的意见。您应当及时关注和了解本协议的修订情况，如果您不同意修订后协议版本，请您立即停止使用本平台，否则即视同您同意并完全接受修订后的协议版本。</view>
			<view class="p">11.2 本协议各条款是可分的，所约定的任何条款如果部分或者全部无效，不影响该条款其他部分及本协议其他条款的法律效力。</view>
			<view class="p">11.3本协议各条款的标题只是为了方便用户阅读而起到提示、醒目的作用，对本协议的解释及适用没有任何指引作用。</view>
			<view class="p">11.4由于互联网高速发展，您与村事宝签署的本协议列明的条款并不能完整罗列并覆盖您与村事宝所有权利与义务，现有的约定也不能保证完全符合未来发展的需求。<text class="line">因此，村事宝隐私权政策、村事宝平台规则均为本协议的补充协议，与本协议不可分割且具有同等法律效力。如您使用村事宝平台服务，视为您同意上述补充协议。</text></view>
			<view class="p">11.5您与村事宝均应当严格履行本协议及其补充协议所约定的各项义务，如发生争议或者纠纷，双方可以友好协商解决;协商不成的，任何一方均可向本协议签订地有管辖权的人民法院提起诉讼。</view>
